import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Accordion from '@mui/material/Accordion';

const dispatchedEvents = [];

const SrsMuiAccordion = function (props, ref) {
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <Accordion {...componentProps}>
      <slot name="accordion_summary" />
      <slot name="accordion_details" />
    </Accordion>
  );
}

const styles = `
  /* Custom styles for the Accordion component */
`;

const props = [
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Accordion if needed
];

const slots = [
  {
    name: "accordion_summary",
    allowed: [],
  },
  {
    name: "accordion_details",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiAccordion),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };